.main-menu {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 150px;
  background-color: #191818;
}
.main-menu dl {
  margin-bottom: 10px;
}
.main-menu dl a {
  display: block;
}
.main-menu dl a:hover {
  text-decoration: none;
}
.main-menu dl dt a {
  line-height: 40px;
  color: #999999;
  border-left: 3px solid #888;
}
.main-menu dl dt a:hover {
  background-color: #555;
}
.main-menu dl dt b {
  display: inline-block;
  margin: 0 0 0 10px;
}
.main-menu dl dt i {
  float: right;
  width: 0;
  height: 0;
  margin: 15px 10px 0 0;
  border-width: 10px 7px 0;
  border-color: #999999 transparent;
  border-style: solid dashed;
  transition: all 0.5s ease;
}
.main-menu dl dd {
  display: none;
  padding: 10px 0 0;
}
.main-menu dl dd a {
  line-height: 30px;
  color: #888;
}
.main-menu dl dd a:hover {
  background-color: #444;
}
.main-menu dl dd a b {
  font-weight: 400;
  margin-left: 20px;
}
.main-menu dl.cur dt a {
  border-left-color: #2aabd2;
  background-color: #666;
  color: #fff;
}
.main-menu dl.cur dt i {
  transform: rotate(-180deg);
}
.main-menu dl.cur dd {
  display: block;
}
.main-menu ul {
  list-style: none;
  padding: 0;
}
.main-content {
  position: absolute;
  left: 150px;
  right: 0;
  bottom: 0;
  top: 0;
  overflow: hidden;
}
.main-content iframe {
  width: 100%;
  height: 100%;
  border-width: 0;
}
pre {
  background-color: transparent;
  border: 0;
}
.main-wrap {
  margin: 10px;
}
.arr-explain-list li {
  padding: 5px 0;
}
/*gird*/
.show-grid .row {
  margin-bottom: 10px;
}
.show-grid [class^="col-"] {
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: rgba(86, 61, 124, 0.15);
  border: 1px solid rgba(86, 61, 124, 0.2);
}
.show-grid [class^="col-"] [class^="col-"] {
  background-color: rgba(86, 61, 124, 0.25);
}
.show-font-icon .icon_lists li {
  height: 200px;
  text-align: center;
  list-style: none !important;
}
.show-font-icon .icon_lists li .evicon {
  font-size: 42px;
  line-height: 100px;
  margin: 10px 0;
  color: #999;
  -webkit-transition: font-size 0.25s ease-out 0s;
  -moz-transition: font-size 0.25s ease-out 0s;
  transition: font-size 0.25s ease-out 0s;
}
.show-font-icon .icon_lists li .evicon:hover {
  font-size: 100px;
}
.show-font-icon .icon_lists li small {
  display: block;
  word-break: break-all;
  margin: 5px 0;
}
